import React from "react";
import App from './App';
import CurrentWeather from './componants/CurrentWeather';
import HourleyWeather from './componants/HourleyWeather';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";


function AppRouter() {
  return (
    <Router>
      <div>
        <nav>
        <Link to="/"><button className="action">Current Weather</button></Link>
        <Link to="/daily-updates"><button className="action">Daily Updates</button></Link>           
        <Link to="/hourley-updates"><button className="action">Hourly Updates</button></Link>
        </nav>

        <Route path="/" exact component={CurrentWeather} />
        <Route path="/daily-updates" exact component={App} />
        <Route path="/hourley-updates" component={HourleyWeather} />
      </div>
    </Router>
  );
}

export default AppRouter;